import "./styles.css";

export default function App() {
  var keys = [];
  if (window.ethereum) {
    keys = Object.keys(window.ethereum);
    keys = keys.filter((key) => key[0] !== "_");
  }

  return (
    <div className="App">
      <h1>Keys on windows.ethereum</h1>
      {keys.map((key) => (
        <div> {key} </div>
      ))}
    </div>
  );
}
